import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)
let status_code = Telegram.WebApp.platform === 'tdesktop' || Telegram.WebApp.platform === 'webz'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => status_code ? import('../views/Errors') : import('../views/Home'),
  },
  {
    path: '/participation',
    name: 'Participation',
    component: () => status_code ? import('../views/Errors') : import('../views/Participation'),
  },
  {
    path: '/qr-code-cashback',
    name: 'QrCodeCashBack',
    component: () => status_code ? import('../views/Errors') : import('../views/QrCodeCashBack'),
  },
  {
    path: '/partners',
    name: 'Partners',
    component: () => status_code ? import('../views/Errors') : import('../views/Partners'),
  },
  {
    path: '/market',
    name: 'Ticket Market',
    component: () => status_code ? import('../views/Errors') : import('../views/Market'),
  },
  {
    path: '/partner/:alias',
    name: 'PartnerView',
    component: () => status_code ? import('../views/Errors') : import('../views/PartnerView'),
  },
  {
    path: '/profile',
    name: 'Profile',
    component: () => status_code ? import('../views/Errors') : import('../views/Profile'),
  },
  {
    path: '/my-tickets',
    name: 'MyTickets',
    component: () => status_code ? import('../views/Errors') : import('../views/MyTickets'),
  },
  {
    path: '/give-a-dream',
    name: 'Give a Dream',
    component: () => status_code ? import('../views/Errors') : import('../views/GiveDream'),
  },
  {
    path: '/settings',
    name: 'Settings',
    component: () => status_code ? import('../views/Errors') : import('../views/Settings'),
  },
  {
    path: '/terms',
    name: 'Terms',
    component: () => status_code ? import('../views/Errors') : import('../views/Terms'),
  },
  {
    path: '/policy',
    name: 'Policy',
    component: () => status_code ? import('../views/Errors') : import('../views/Policy'),
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
