import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import router from "@/router";
router

const API_URL = 'https://server.gold-ticket.uz/api';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        user_id: null,
        agreed: null,
        lang: null,
        name: null,
        phone: null,
        photo: null,
        premium: null,
        premium_class: null,
        username: null,
        tickets: null,
        tickets_count: 0,
        errors: null,
        error: null,
        platform: Telegram.WebApp.platform,
        bottomNavigationMenu: [],
    },
    getters: {},
    mutations: {
        setUser(state, data){
            state.user_id = data.user_id;
            state.agreed = data.agreed;
            state.lang = data.lang;
            state.name = data.name;
            state.phone = data.phone;
            state.photo = data.photo;
            state.premium_class = data.subscription ? '' : 'active';
            state.premium = data.subscription;
            state.username = data.username;
        },
        setTickets(state, data){
            state.tickets = data.data;
            state.tickets_count = data.total;
        },
        setBottomNavigationMenu(state, lang){
            state.bottomNavigationMenu =  [
                {
                    className: 'home',
                    name: 'home',
                    title: lang === 'ru' ? 'Главное' : 'Asosiy'
                },
                {
                    className: 'market',
                    name: 'bag-handle',
                    title: lang === 'ru' ? 'Маркет' : 'Market'
                },
                {
                    className: 'partners',
                    name: 'star',
                    title: lang === 'ru' ? 'Спонсоры' : 'Homiylar'
                },
                {
                    className: 'profile',
                    name: 'person',
                    title: lang === 'ru' ? 'Профиль' : 'Profil'
                },
            ]
        },
        userUpdateProfile(state, data) {
            if (data === true) {
                Telegram.WebApp.MainButton.hideProgress()
                if (state.lang === 'ru') {
                    Telegram.WebApp.showPopup({
                        'title': 'Успешно',
                        'message': 'Ваши данные успешно сохранены',
                        'buttons': [
                            {
                                'id': 'success',
                                'type': 'ok',
                                'text': "Ок"
                            }
                        ]
                    })
                } else {
                    Telegram.WebApp.showPopup({
                        'title': 'Muvofaqiyatli!',
                        'message': 'Sizning malumotlaringiz muvofatqiyatli saqlandi',
                        'buttons': [
                            {
                                'id': 'success',
                                'type': 'ok',
                                'text': "Ok"
                            }
                        ]
                    })
                }
                Telegram.WebApp.MainButton.hide()
                router.push('/')
            } else {
                Telegram.WebApp.showPopup(data)
            }
        },
        userAgreedAccess(state, status) {
            if (status === true) {
                state.agreed = true;
                router.push('/')
            }
        },
        error(data) {
            if (data.errors) {
                Telegram.WebApp.showPopup = data.errors;
            } else {
                Telegram.WebApp.showPopup = data.error;
            }
        }
    },
    actions: {
        async getUser({ commit }, user_id) {
            try {
                const res = await axios.get(`${API_URL}/user/${user_id}`)
                res.data.data.user_id = user_id
                commit("setUser", res.data.data);
                commit("setBottomNavigationMenu", res.data.data.lang);
            } catch (e) {
                commit("error", e.response.data);
                throw e;
            }
        },
        async updateUser({ commit }, data) {
            try {
                await axios.post(`${API_URL}/user/${data.user_id}`, data.data)
                commit("userUpdateProfile", true)
            } catch (e) {
                commit("userUpdateProfile", data)
            }
        },
        async userAgreed({ commit }, data) {
            try {
                await axios.post(`${API_URL}/user/${data.user_id}`, data.data)
                commit("userAgreedAccess", true)
            } catch (e) {
                commit("error", false)
            }
        },
        async getTickets({ commit }, user_id) {
            try {
                const res = await axios.get(`${API_URL}/ticket/${user_id}`)
                commit("setTickets", res.data)
            } catch (e) {
                commit("error", e.response.data)
            }
        },
    },
})
