<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>
<script>
export default {
  name: 'App',
  async mounted() {
    document.title = 'Golden Tickets';


    if (Object.keys(Telegram.WebApp.initDataUnsafe).length === 0 || typeof Telegram.WebApp.initDataUnsafe.user === 'undefined') {
      window.location.href = 'https://gold-ticket.uz'
    } else {
      // const urlParams = new URLSearchParams(window.location.search);
      // if (!sessionStorage.getItem('__sombra_gt_appLang') || sessionStorage.getItem('__sombra_gt_appLang') !== urlParams.get('lang')) {
      //   sessionStorage.setItem('__sombra_gt_appLang', urlParams.get('lang'))
      // }
      document.body.setAttribute('app-color-scheme', Telegram.WebApp.colorScheme)
      Telegram.WebApp.onEvent('themeChanged', function(){
        document.body.setAttribute('app-color-scheme', Telegram.WebApp.colorScheme)
      });

      Telegram.WebApp.expand()
      document.body.classList.add(Telegram.WebApp.platform)
      if (Telegram.WebApp.colorScheme === 'dark') {
        Telegram.WebApp.setBackgroundColor = "#1c1c1c";
        Telegram.WebApp.themeParams.button_color = "#e5ab34";
      } else {
        Telegram.WebApp.setBackgroundColor = "#e2e2e2";
        Telegram.WebApp.themeParams.button_color = "#e5ab34";
      }

      await this.$store.dispatch('getUser', Telegram.WebApp.initDataUnsafe.user.id)
      if (!this.$store.state.agreed) {
        this.$router.push('/terms');
      }
    }
  }
}
</script>

<style scoped>
#app {
  display: flex;
  width: 100%;
}
</style>
